.quick-contact {
  z-index: 999;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

@media (width <= 980px) {
  .why-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .banner-container {
    flex-direction: column;
  }
}

@media (width <= 576px) {
  .why-grid, .card-grid {
    grid-template-columns: repeat(1, 1fr) !important;
  }
}

@media screen and (width <= 576px) {
  .site-header .actions a.btn {
    display: none;
  }
}

.hero-photo img {
  width: 100%;
  height: 320px;
}

:root {
  --navy: #0b1d39;
  --navy-deep: #050b1a;
  --maroon: #8b1e3f;
  --maroon-dark: #701731;
  --maroon-glow: #c73659;
  --light: #f8f9fb;
  --white: #fff;
  --text: #1d2433;
  --muted: #4b5565;
  --card: #fff;
  --card-dark: #1a1f2c;
  --bluegrey: #101b2d;
  --shadow: 0 10px 30px #0b1d391f;
}

* {
  box-sizing: border-box;
}

html, body {
  color: var(--text);
  background: #fff;
  margin: 0;
  padding: 0;
  font-family: Poppins, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, Apple Color Emoji, Segoe UI Emoji;
}

a {
  color: var(--maroon);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.container {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 20px;
}

.site-header {
  z-index: 20;
  background: #fff;
  border-bottom: 1px solid #eaeaea;
  position: sticky;
  top: 0;
}

.nav-bar {
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  height: 72px;
  display: flex;
}

.logo {
  color: var(--navy);
  align-items: center;
  gap: 10px;
  font-weight: 700;
  display: flex;
}

.logo-mark {
  background: var(--navy);
  color: #fff;
  border-radius: 9px;
  place-items: center;
  width: 36px;
  height: 36px;
  display: inline-grid;
}

.logo-type {
  white-space: nowrap;
}

.nav ul {
  gap: 18px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav a {
  color: var(--navy);
  font-weight: 500;
}

.nav-toggle {
  display: none;
}

.actions {
  align-items: center;
  gap: 12px;
  display: flex;
}

.btn {
  box-shadow: none;
  border: 2px solid #0000;
  border-radius: 999px;
  justify-content: center;
  align-items: center;
  padding: 12px 18px;
  font-weight: 600;
  transition: all .3s;
  display: inline-flex;
}

.btn.primary {
  background: var(--maroon);
  color: #fff;
}

.btn.primary:hover {
  background: #701731;
  box-shadow: 0 6px 18px #c7365940;
}

.btn.outline {
  border-color: var(--maroon);
  color: var(--maroon);
  background: none;
}

.btn.outline:hover {
  background: var(--maroon);
  color: #fff;
}

.btn.inverse {
  color: var(--navy);
  background: #fff;
}

.btn.inverse:hover {
  background: #eaeaea;
}

.btn.text {
  border: none;
  padding: 0;
}

.btn.light {
  color: var(--maroon);
  background: #fff;
}

.btn.light:hover {
  background: #eaeaea;
}

.btn.ghost {
  color: #fff;
  background: none;
  border-color: #fff;
}

.btn.ghost:hover {
  color: var(--maroon);
  background: #fff;
}

.toggle {
  border: 2px solid var(--maroon);
  background: var(--light);
  cursor: pointer;
  border-radius: 999px;
  width: 48px;
  height: 24px;
  position: relative;
}

.toggle .icon {
  pointer-events: none;
  font-size: 12px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.toggle .sun {
  color: #fff;
  display: none;
  left: 6px;
}

.toggle .moon {
  right: 6px;
}

.hero {
  isolation: isolate;
  background: var(--navy);
  color: #fff;
  position: relative;
  overflow: hidden;
}

.hero-gradient {
  background: linear-gradient(-45deg, var(--navy), var(--maroon), var(--navy));
  opacity: .5;
  z-index: 0;
  background-size: 400% 400%;
  animation: 25s infinite gradientMove;
  position: absolute;
  inset: 0;
}

.hero-wrap {
  z-index: 1;
  grid-template-columns: 1.1fr .9fr;
  align-items: center;
  gap: 24px;
  min-height: 540px;
  display: grid;
  position: relative;
}

.hero h1 {
  margin: 0 0 12px;
  font-size: 44px;
  line-height: 1.1;
}

.hero .sub {
  opacity: .95;
  font-size: 18px;
}

.hero .cta-row {
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 16px;
  display: flex;
}

.hero-photo {
  width: 100%;
  height: 320px;
  box-shadow: var(--shadow);
  background: linear-gradient(135deg, #ffffff1a, #fff0), url("hero-lab.38d394c2.jpg") center / cover no-repeat;
  border-radius: 20px;
}

.why {
  padding: 64px 0;
}

.why-grid {
  grid-template-columns: repeat(5, 1fr);
  gap: 18px;
  margin: 24px 0 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.why-grid li {
  background: var(--card);
  box-shadow: var(--shadow);
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 16px;
  transition: all .3s;
}

.why-grid li:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 28px #0000001f;
}

.why-grid .ic {
  color: var(--maroon);
  font-size: 22px;
}

.courses-preview {
  background: var(--light);
  padding: 64px 0;
}

.lead {
  color: var(--muted);
}

.card-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 18px;
  margin-top: 18px;
  display: grid;
}

.card {
  background: var(--card);
  color: inherit;
  box-shadow: var(--shadow);
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 16px;
  transition: all .3s;
  display: block;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 14px 32px #00000024;
}

.card .link {
  color: var(--maroon);
  margin-top: 8px;
  font-weight: 600;
  display: inline-block;
}

.card-img {
  background: linear-gradient(135deg, #e9edf7, #f7e9ee);
  border-radius: 12px;
  height: 150px;
}

.img-eda, .img-layout, .img-layout2, .img-fpga, .img-embedded, .img-analog {
  background-image: linear-gradient(135deg, #e9edf7, #f7e9ee);
}

.placement-strip {
  background: var(--navy);
  color: #fff;
  padding: 48px 0;
}

.placement-flex {
  grid-template-columns: 1.2fr .8fr;
  align-items: center;
  gap: 24px;
  display: grid;
}

.logos {
  opacity: .85;
  flex-wrap: wrap;
  gap: 18px;
  display: flex;
}

.placement-box {
  color: var(--navy);
  box-shadow: var(--shadow);
  border: 2px solid var(--maroon);
  background: #fff;
  border-radius: 16px;
  padding: 16px;
}

.projects {
  padding: 64px 0;
}

.tabs {
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 12px;
  display: flex;
}

.tab {
  border: 1px solid var(--maroon);
  color: var(--maroon);
  cursor: pointer;
  background: #fff;
  border-radius: 999px;
  padding: 8px 14px;
  font-weight: 600;
  transition: all .3s;
}

.tab.active, .tab:hover {
  background: var(--maroon);
  color: #fff;
}

.tab-panels .panel {
  display: none;
}

.tab-panels .panel.show {
  display: block;
}

.card-grid.sm {
  grid-template-columns: repeat(3, 1fr);
}

.card.mini {
  padding: 14px;
}

.about-preview {
  background: var(--light);
  padding: 64px 0;
}

.about-flex {
  grid-template-columns: .8fr 1.2fr;
  align-items: center;
  gap: 24px;
  display: grid;
}

.about-photo {
  height: 320px;
  box-shadow: var(--shadow);
  background: linear-gradient(135deg, #0b1d3926, #8b1e3f26), url("about-lab.38d394c2.jpg") center / cover no-repeat;
  border-radius: 20px;
}

.admissions-cta {
  background: var(--maroon);
  color: #fff;
  text-align: center;
  padding: 64px 0;
}

.admissions-cta.slim {
  padding: 48px 0;
}

.steps {
  justify-content: center;
  gap: 18px;
  margin: 18px 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.steps li {
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 12px;
  padding: 10px 14px;
}

.page-hero {
  background: linear-gradient(135deg, #0b1d390f, #8b1e3f0f);
  padding: 64px 0;
}

.page-hero h1 {
  margin: 0 0 8px;
}

.course-list {
  padding: 32px 0 64px;
}

.course {
  background: var(--card);
  box-shadow: var(--shadow);
  border: 1px solid #eee;
  border-radius: 16px;
  margin: 18px 0;
  padding: 20px;
}

.course .cols {
  grid-template-columns: 1.3fr .7fr;
  gap: 18px;
  display: grid;
}

.two-col {
  grid-template-columns: 1.2fr .8fr;
  gap: 24px;
  padding: 48px 0;
  display: grid;
}

.profile .avatar {
  background: linear-gradient(135deg, #e9edf7, #f7e9ee);
  border-radius: 16px;
  width: 100%;
  height: 220px;
}

.partner-logos .logos {
  flex-wrap: wrap;
  gap: 12px;
  display: flex;
}

.partner-logos .logos span {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 10px;
  padding: 6px 10px;
}

.contact-grid {
  grid-template-columns: 1.1fr .9fr;
  gap: 24px;
  padding: 48px 0;
  display: grid;
}

.contact-form {
  background: var(--card);
  box-shadow: var(--shadow);
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 20px;
}

.contact-form label {
  margin: 10px 0;
  display: block;
}

.contact-form input, .contact-form select, .contact-form textarea {
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 100%;
  padding: 10px;
}

.contact-form button {
  margin-top: 8px;
}

.contact-info .map-embed iframe {
  border: 0;
  border-radius: 12px;
  width: 100%;
  height: 280px;
}

.site-footer {
  background: var(--navy);
  color: #fff;
  margin-top: 48px;
}

.footer-grid {
  grid-template-columns: 1.2fr 1fr 1fr;
  gap: 24px;
  padding: 32px 0;
  display: grid;
}

.footer-grid a {
  color: #fff;
  text-decoration: underline;
}

.social a {
  border: 1px solid #ffffff80;
  border-radius: 9px;
  place-items: center;
  width: 36px;
  height: 36px;
  margin-right: 8px;
  display: inline-grid;
}

.legal {
  opacity: .9;
  border-top: 1px solid #fff3;
  padding: 12px 0;
}

#skip a {
  width: 1px;
  height: 1px;
  position: absolute;
  top: auto;
  left: -9999px;
  overflow: hidden;
}

#skip a:focus {
  color: #fff;
  z-index: 100;
  background: #000;
  border-radius: 8px;
  width: auto;
  height: auto;
  padding: 8px 10px;
  top: 16px;
  left: 16px;
}

@media (width <= 980px) {
  .hero-wrap {
    grid-template-columns: 1fr;
  }

  .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .placement-flex, .about-flex, .two-col, .contact-grid, .course .cols {
    grid-template-columns: 1fr;
  }

  .nav ul {
    box-shadow: var(--shadow);
    background: #fff;
    border: 1px solid #eaeaea;
    border-radius: 12px;
    padding: 10px 14px;
    display: none;
    position: absolute;
    top: 72px;
    right: 20px;
  }

  .nav ul.show {
    gap: 8px;
    display: grid;
  }

  .nav-toggle {
    background: none;
    border: none;
    font-size: 20px;
    display: inline-flex;
  }
}

@keyframes gradientMove {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

body.dark {
  color: #d0d4dc;
  background: #0b0f1f;
}

body.dark .site-header {
  background: #0b0f1f;
  border-color: #182036;
}

body.dark .nav a {
  color: #b42261;
}

body.dark .hero {
  background: var(--navy-deep);
}

body.dark .hero-gradient {
  opacity: .6;
  background: linear-gradient(-45deg, var(--navy-deep), var(--maroon-dark), var(--navy-deep));
  animation-duration: 30s;
}

body.dark .card, body.dark .course, body.dark .why-grid li {
  background: var(--card-dark);
  border-color: #2b3040;
}

body.dark .courses-preview {
  background: #0f1322;
}

body.dark .placement-strip {
  background: var(--navy-deep);
}

body.dark .admissions-cta {
  background: var(--maroon-dark);
}

body.dark .page-hero {
  background: linear-gradient(135deg, #0b1d392e, #8b1e3f2e);
}

body.dark .site-footer {
  background: #050b1a;
}

body.dark .btn.primary {
  color: var(--maroon);
  background: #fff;
}

body.dark .btn.primary:hover {
  background: #eaeaea;
  box-shadow: 0 6px 18px #c7365959;
}

body.dark .btn.outline {
  color: #fff;
  border-color: #fff;
}

body.dark .btn.outline:hover {
  color: var(--navy);
  background: #fff;
}

body.dark .btn.inverse {
  color: #fff;
  background: none;
  border-color: #fff;
}

body.dark .btn.inverse:hover {
  color: var(--navy);
  background: #fff;
}

body.dark .toggle {
  background: var(--navy);
  border-color: var(--maroon);
}

body.dark .toggle .sun {
  display: block;
}

body.dark .toggle .moon {
  display: none;
}

.course-catalog {
  padding: 32px 0 16px;
}

.catalog-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  display: grid;
}

.catalog-card {
  background: var(--card);
  text-align: left;
  box-shadow: var(--shadow);
  cursor: pointer;
  border: 1px solid #0000;
  border-radius: 16px;
  padding: 18px;
  transition: all .25s;
}

.catalog-card:hover {
  border-color: var(--maroon);
  transform: translateY(-3px);
  box-shadow: 0 12px 28px #0000001f;
}

.catalog-card h3 {
  color: var(--navy);
  margin: 0 0 6px;
}

.catalog-card p {
  color: var(--muted);
  margin: 0 0 8px;
}

.catalog-card span {
  color: var(--maroon);
  font-weight: 600;
}

.back-link {
  align-items: center;
  gap: 6px;
  margin: 10px 0 8px;
  display: inline-flex;
}

.course-list[data-mode="list"] .course, .course-list[data-mode="detail"] .course {
  display: none;
}

.course-list[data-mode="detail"] .course.is-active {
  display: block;
}

body.dark .catalog-card {
  background: var(--card-dark);
  border-color: #2b3040;
}

body.dark .catalog-card h3 {
  color: #fff;
}

body.dark .catalog-card p {
  color: #d0d4dc;
}

body.dark .catalog-card span {
  color: #fff;
}

@media (width <= 980px) {
  .catalog-grid {
    grid-template-columns: 1fr;
  }
}

.course-list .course {
  display: none;
}

.course-list .course.is-active {
  display: block;
}

.page-hero {
  color: var(--text);
  position: relative;
  overflow: hidden;
}

.page-hero:before {
  content: "";
  pointer-events: none;
  background: linear-gradient(-45deg, #0b1d391a, #8b1e3f1a, #0b1d391a) 0 0 / 400% 400%;
  animation: 25s infinite gradientMove;
  position: absolute;
  inset: 0;
}

.page-hero .container {
  z-index: 1;
  position: relative;
}

.page-hero p {
  max-width: 920px;
}

.pre {
  opacity: 0;
  transition: opacity .5s, transform .5s;
  transform: translateY(12px);
}

.in {
  opacity: 1;
  transform: none;
}

.nav a.active {
  color: var(--maroon);
  text-decoration: underline;
}

h1, h2 {
  letter-spacing: .2px;
}

h2 {
  margin-top: 0;
}

body.dark .page-hero:before {
  background: linear-gradient(-45deg, #050b1a59, #70173147, #050b1a59);
  animation-duration: 30s;
}

.card h3 {
  margin-top: 10px;
}

.card p {
  color: var(--muted);
}

.btn:focus {
  outline-offset: 2px;
  outline: 3px solid #c7365959;
}

body.dark .btn:focus {
  outline-color: #ffffff59;
}

.highlights-hero {
  background: linear-gradient(135deg, var(--maroon), var(--navy));
  color: #fff;
  text-align: center;
  padding: 80px 20px;
}

.highlights-stats {
  text-align: center;
  justify-content: center;
  gap: 20px;
  margin: 50px 0;
  display: flex;
}

.highlights-stats .stat {
  color: var(--maroon);
  font-size: 2rem;
  font-weight: bold;
}

.highlights-stats .stat p {
  color: var(--text);
  font-size: 1rem;
}

.highlights-cards {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.highlights-cards .card {
  background: var(--card);
  box-shadow: var(--shadow);
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
}

.highlights-cards .card:hover {
  transform: translateY(-5px);
  box-shadow: 0 14px 28px #00000026;
}

.highlights-stats .stat {
  color: var(--maroon);
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  transition: transform .3s;
  display: flex;
}

.highlights-stats .stat:hover {
  transform: scale(1.05);
}

.highlights-stats .icon {
  color: var(--maroon);
  margin-bottom: 8px;
  animation: 1.5s infinite bounce;
}

.highlights-stats .icon svg {
  width: 48px;
  height: 48px;
}

.highlights-stats .count {
  font-size: 2rem;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-5px);
  }
}

.site-header .container.nav-bar > a {
  align-items: center;
  margin: 0;
  padding-left: 10px;
  display: flex;
}

.site-logo {
  width: auto;
  height: 50px;
  display: block;
}

@media (width <= 500px) {
  .site-logo {
    height: 45px;
  }
}

.why-grid .ic {
  color: var(--maroon);
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  display: inline-flex;
}

.why-grid .ic svg {
  width: 36px;
  height: 36px;
}

.why-grid li:hover .ic {
  color: var(--navy);
  transition: color .3s;
}

.site-header .container {
  padding-left: 0;
}

.site-header .container.nav-bar > a {
  margin-left: 0;
}

.quick-contact {
  z-index: 9999;
  flex-direction: column;
  gap: 16px;
  animation: .8s ease-out slideIn;
  display: flex;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.quick-contact .btn {
  background-color: #666;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 56px;
  transition: transform .3s, background-color .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 10px #00000040;
}

.quick-contact .btn .icon {
  width: 28px;
  height: 28px;
}

.quick-contact .btn:hover {
  transform: scale(1.15)rotate(8deg);
}

.quick-contact .btn .tooltip {
  color: #fff;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  background: #000000d9;
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 13px;
  transition: opacity .3s;
  position: absolute;
  top: 50%;
  right: 70px;
  transform: translateY(-50%);
}

.quick-contact .btn:hover .tooltip {
  opacity: 1;
}

.quick-contact .whatsapp {
  background-color: #25d366;
}

.quick-contact .whatsapp:hover {
  background-color: #1ebe5b;
}

.quick-contact .call {
  background-color: #e63946;
}

.quick-contact .call:hover {
  background-color: #cc2e3c;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(100px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.banner-container {
  max-width: 100%;
  height: 400px;
  position: relative;
  overflow: hidden;
}

.banner-slide {
  width: 100%;
  height: 100%;
  display: none;
