
@import "./floatingbutton.css";
@import "./responsive.css";
@import "./header.css";
@import "./carousel.css";

:root{
  --navy:#0B1D39;
  --navy-deep:#050B1A;
  --maroon:#8B1E3F;
  --maroon-dark:#701731;
  --maroon-glow:#C73659;
  --light:#F8F9FB;
  --white:#FFFFFF;
  --text:#1D2433;
  --muted:#4B5565;
  --card:#FFFFFF;
  --card-dark:#1A1F2C;
  --bluegrey:#101B2D;
  --shadow:0 10px 30px rgba(11,29,57,.12);
}
*{box-sizing:border-box}
html,body{margin:0;padding:0;font-family:Poppins,system-ui,-apple-system,Segoe UI,Roboto,"Helvetica Neue",Arial,"Noto Sans","Apple Color Emoji","Segoe UI Emoji";color:var(--text);background:#fff}
a{color:var(--maroon);text-decoration:none}
a:hover{text-decoration:underline}
.container{max-width:1160px;margin:0 auto;padding:0 20px}
.site-header{position:sticky;top:0;z-index:20;background:#fff;border-bottom:1px solid #eaeaea}
.nav-bar{display:flex;align-items:center;gap:16px;justify-content:space-between;height:72px}
.logo{display:flex;align-items:center;gap:10px;font-weight:700;color:var(--navy)}
.logo-mark{display:inline-grid;place-items:center;width:36px;height:36px;border-radius:9px;background:var(--navy);color:#fff}
.logo-type{white-space:nowrap}
.nav ul{display:flex;gap:18px;list-style:none;margin:0;padding:0}
.nav a{color:var(--navy);font-weight:500}
.nav-toggle{display:none}
.actions{display:flex;align-items:center;gap:12px}
.btn{display:inline-flex;align-items:center;justify-content:center;padding:12px 18px;border-radius:999px;border:2px solid transparent;font-weight:600;transition:.3s;box-shadow:none}
.btn.primary{background:var(--maroon);color:#fff}
.btn.primary:hover{background:#701731;box-shadow:0 6px 18px rgba(199,54,89,.25)}
.btn.outline{background:transparent;border-color:var(--maroon);color:var(--maroon)}
.btn.outline:hover{background:var(--maroon);color:#fff}
.btn.inverse{background:#fff;color:var(--navy)}
.btn.inverse:hover{background:#EAEAEA}
.btn.text{padding:0;border:none}
.btn.light{background:#fff;color:var(--maroon)}
.btn.light:hover{background:#EAEAEA}
.btn.ghost{background:transparent;border-color:#fff;color:#fff}
.btn.ghost:hover{background:#fff;color:var(--maroon)}
.toggle{width:48px;height:24px;border-radius:999px;border:2px solid var(--maroon);background:var(--light);position:relative;cursor:pointer}
.toggle .icon{position:absolute;top:50%;transform:translateY(-50%);font-size:12px;pointer-events:none}
.toggle .sun{left:6px;color:#fff;display:none}
.toggle .moon{right:6px}
/* Hero */
.hero{position:relative;isolation:isolate;background:var(--navy);color:#fff;overflow:hidden}
.hero-gradient{position:absolute;inset:0;background:linear-gradient(-45deg,var(--navy),var(--maroon),var(--navy));background-size:400% 400%;animation:gradientMove 25s ease infinite;opacity:.5;z-index:0}
.hero-wrap{position:relative;z-index:1;display:grid;grid-template-columns:1.1fr .9fr;gap:24px;min-height:540px;align-items:center}
.hero h1{font-size:44px;line-height:1.1;margin:0 0 12px}
.hero .sub{font-size:18px;opacity:.95}
.hero .cta-row{display:flex;gap:12px;flex-wrap:wrap;margin-top:16px}
.hero-photo{width:100%;height:320px;border-radius:20px;background:linear-gradient(135deg,rgba(255,255,255,.1),rgba(255,255,255,.0)),url('../assets/img/hero-lab.jpg') center/cover no-repeat;box-shadow:var(--shadow)}
/* Why */
.why{padding:64px 0}
.why-grid{display:grid;grid-template-columns:repeat(5,1fr);gap:18px;list-style:none;padding:0;margin:24px 0 0}
.why-grid li{background:var(--card);border:1px solid #eee;border-radius:16px;padding:16px;box-shadow:var(--shadow);transition:.3s}
.why-grid li:hover{transform:translateY(-3px);box-shadow:0 12px 28px rgba(0,0,0,.12)}
.why-grid .ic{font-size:22px;color:var(--maroon)}
/* Courses preview */
.courses-preview{padding:64px 0;background:var(--light)}
.lead{color:var(--muted)}
.card-grid{display:grid;grid-template-columns:repeat(3,1fr);gap:18px;margin-top:18px}
.card{background:var(--card);border:1px solid #eee;border-radius:16px;padding:16px;display:block;color:inherit;box-shadow:var(--shadow);transition:.3s}
.card:hover{transform:translateY(-4px);box-shadow:0 14px 32px rgba(0,0,0,.14)}
.card .link{display:inline-block;margin-top:8px;font-weight:600;color:var(--maroon)}
.card-img{height:150px;border-radius:12px;background:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-eda{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-layout{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-layout2{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-fpga{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-embedded{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.img-analog{background-image:linear-gradient(135deg,#e9edf7,#f7e9ee)}
/* Placement strip */
.placement-strip{padding:48px 0;background:var(--navy);color:#fff}
.placement-flex{display:grid;grid-template-columns:1.2fr .8fr;gap:24px;align-items:center}
.logos{display:flex;gap:18px;flex-wrap:wrap;opacity:.85}
.placement-box{background:#fff;color:var(--navy);border-radius:16px;padding:16px;box-shadow:var(--shadow);border:2px solid var(--maroon)}
/* Projects */
.projects{padding:64px 0}
.tabs{display:flex;gap:8px;flex-wrap:wrap;margin-bottom:12px}
.tab{padding:8px 14px;border-radius:999px;border:1px solid var(--maroon);background:#fff;color:var(--maroon);font-weight:600;cursor:pointer;transition:.3s}
.tab.active,.tab:hover{background:var(--maroon);color:#fff}
.tab-panels .panel{display:none}
.tab-panels .panel.show{display:block}
.card-grid.sm{grid-template-columns:repeat(3,1fr)}
.card.mini{padding:14px}
/* About preview */
.about-preview{padding:64px 0;background:var(--light)}
.about-flex{display:grid;grid-template-columns:.8fr 1.2fr;gap:24px;align-items:center}
.about-photo{height:320px;border-radius:20px;background:linear-gradient(135deg,rgba(11,29,57,.15),rgba(139,30,63,.15)),url('../assets/img/about-lab.jpg') center/cover no-repeat;box-shadow:var(--shadow)}
/* Admissions CTA */
.admissions-cta{padding:64px 0;background:var(--maroon);color:#fff;text-align:center}
.admissions-cta.slim{padding:48px 0}
.steps{display:flex;gap:18px;justify-content:center;list-style:none;padding:0;margin:18px 0}
.steps li{background:rgba(255,255,255,.1);border:1px solid rgba(255,255,255,.2);padding:10px 14px;border-radius:12px}
/* Page hero */
.page-hero{padding:64px 0;background:linear-gradient(135deg,rgba(11,29,57,.06),rgba(139,30,63,.06))}
.page-hero h1{margin:0 0 8px}
/* Course page */
.course-list{padding:32px 0 64px}
.course{background:var(--card);border:1px solid #eee;border-radius:16px;padding:20px;margin:18px 0;box-shadow:var(--shadow)}
.course .cols{display:grid;grid-template-columns:1.3fr .7fr;gap:18px}
/* Two-col generic */
.two-col{display:grid;grid-template-columns:1.2fr .8fr;gap:24px;padding:48px 0}
.profile .avatar{width:100%;height:220px;border-radius:16px;background:linear-gradient(135deg,#e9edf7,#f7e9ee)}
.partner-logos .logos{display:flex;flex-wrap:wrap;gap:12px}
.partner-logos .logos span{background:#fff;border:1px solid #eee;border-radius:10px;padding:6px 10px}
/* Contact */
.contact-grid{display:grid;grid-template-columns:1.1fr .9fr;gap:24px;padding:48px 0}
.contact-form{background:var(--card);border:1px solid #eee;border-radius:16px;padding:20px;box-shadow:var(--shadow)}
.contact-form label{display:block;margin:10px 0}
.contact-form input,.contact-form select,.contact-form textarea{width:100%;padding:10px;border:1px solid #ddd;border-radius:10px}
.contact-form button{margin-top:8px}
.contact-info .map-embed iframe{width:100%;height:280px;border:0;border-radius:12px}
/* Footer */
.site-footer{background:var(--navy);color:#fff;margin-top:48px}
.footer-grid{display:grid;grid-template-columns:1.2fr 1fr 1fr;gap:24px;padding:32px 0}
.footer-grid a{color:#fff;text-decoration:underline}
.social a{display:inline-grid;place-items:center;width:36px;height:36px;border:1px solid rgba(255,255,255,.5);border-radius:9px;margin-right:8px}
.legal{padding:12px 0;border-top:1px solid rgba(255,255,255,.2);opacity:.9}
/* Skip link */
#skip a{position:absolute;left:-9999px;top:auto;width:1px;height:1px;overflow:hidden}
#skip a:focus{left:16px;top:16px;width:auto;height:auto;background:#000;color:#fff;padding:8px 10px;border-radius:8px;z-index:100}
/* Responsive */
@media (max-width: 980px){
  .hero-wrap{grid-template-columns:1fr}
  .card-grid{grid-template-columns:repeat(2,1fr)}
  .placement-flex{grid-template-columns:1fr}
  .about-flex,.two-col,.contact-grid,.course .cols{grid-template-columns:1fr}
  .nav ul{display:none;position:absolute;right:20px;top:72px;background:#fff;border:1px solid #eaeaea;border-radius:12px;padding:10px 14px;box-shadow:var(--shadow)}
  .nav ul.show{display:grid;gap:8px}
  .nav-toggle{display:inline-flex;background:transparent;border:none;font-size:20px}
}
@keyframes gradientMove{
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}

/* Dark mode */
body.dark{
  background:#0B0F1F;color:#D0D4DC;
}
body.dark .site-header{background:#0B0F1F;border-color:#182036}
body.dark .nav a{color:#b42261}
body.dark .hero{background:var(--navy-deep)}
body.dark .hero-gradient{opacity:.6;background:linear-gradient(-45deg,var(--navy-deep),var(--maroon-dark),var(--navy-deep));animation-duration:30s}
body.dark .card, body.dark .course{background:var(--card-dark);border-color:#2b3040}
body.dark .why-grid li{background:var(--card-dark);border-color:#2b3040}
body.dark .courses-preview{background:#0f1322}
body.dark .placement-strip{background:var(--navy-deep)}
body.dark .admissions-cta{background:var(--maroon-dark)}
body.dark .page-hero{background:linear-gradient(135deg,rgba(11,29,57,.18),rgba(139,30,63,.18))}
body.dark .site-footer{background:#050B1A}
body.dark .btn.primary{background:#fff;color:var(--maroon)}
body.dark .btn.primary:hover{background:#EAEAEA;box-shadow:0 6px 18px rgba(199,54,89,.35)}
body.dark .btn.outline{border-color:#fff;color:#fff}
body.dark .btn.outline:hover{background:#fff;color:var(--navy)}
body.dark .btn.inverse{background:transparent;color:#fff;border-color:#fff}
body.dark .btn.inverse:hover{background:#fff;color:var(--navy)}
body.dark .toggle{background:var(--navy);border-color:var(--maroon)}
body.dark .toggle .sun{display:block}
body.dark .toggle .moon{display:none}

/* Catalog list view for Courses page */
.course-catalog{padding:32px 0 16px}
.catalog-grid{display:grid;grid-template-columns:repeat(3,1fr);gap:16px}
.catalog-card{background:var(--card);border:1px solid #eee;border-radius:16px;padding:18px;text-align:left;box-shadow:var(--shadow);cursor:pointer;transition:.25s;border-color:transparent}
.catalog-card:hover{transform:translateY(-3px);box-shadow:0 12px 28px rgba(0,0,0,.12);border-color:var(--maroon)}
.catalog-card h3{margin:0 0 6px;color:var(--navy)}
.catalog-card p{margin:0 0 8px;color:var(--muted)}
.catalog-card span{font-weight:600;color:var(--maroon)}
.back-link{margin:10px 0 8px;display:inline-flex;align-items:center;gap:6px}

/* List → Detail behavior (default: list) */
.course-list .course{display:block}
.course-list[data-mode="list"] .course{display:none} /* hide details when in list mode */
.course-list[data-mode="detail"] .course{display:none}
.course-list[data-mode="detail"] .course.is-active{display:block}

/* Dark mode adjustments */
body.dark .catalog-card{background:var(--card-dark);border-color:#2b3040}
body.dark .catalog-card h3{color:#fff}
body.dark .catalog-card p{color:#D0D4DC}
body.dark .catalog-card span{color:#fff}
@media (max-width:980px){
  .catalog-grid{grid-template-columns:1fr}
}

/* Force default state: show catalog, hide details until a course is selected */
.course-list .course{display:none}
.course-list .course.is-active{display:block}

/* Animated gradient for page-hero like homepage hero */
.page-hero{
  position:relative;
  overflow:hidden;
  color:var(--text);
}
.page-hero::before{
  content:"";
  position:absolute; inset:0;
  background: linear-gradient(-45deg, rgba(11,29,57,.10), rgba(139,30,63,.10), rgba(11,29,57,.10));
  background-size: 400% 400%;
  animation: gradientMove 25s ease infinite;
  pointer-events:none;
}
.page-hero .container{position:relative; z-index:1;}
.page-hero p{max-width:920px}

/* Reveal animation */
.pre{opacity:0; transform: translateY(12px); transition: opacity .5s ease, transform .5s ease}
.in{opacity:1; transform:none}

/* Navbar active link */
.nav a.active{color:var(--maroon); text-decoration:underline}

/* Global headings spacing */
h1,h2{letter-spacing:.2px}
h2{margin-top:0}

/* Dark mode page-hero */
body.dark .page-hero::before{
  background: linear-gradient(-45deg, rgba(5,11,26,.35), rgba(112,23,49,.28), rgba(5,11,26,.35));
  animation-duration: 30s;
}

/* Card polish */
.card h3{margin-top:10px}
.card p{color:var(--muted)}

/* Ensure consistent button focus */
.btn:focus{outline:3px solid rgba(199,54,89,.35); outline-offset:2px}
body.dark .btn:focus{outline-color: rgba(255,255,255,.35)}

/* Highlights Page Styling */
.highlights-hero {
  background: linear-gradient(135deg, var(--maroon), var(--navy));
  color: white;
  text-align: center;
  padding: 80px 20px;
}
.highlights-stats {
  display: flex;
  gap: 20px;
  justify-content: center;
  text-align: center;
  margin: 50px 0;
}
.highlights-stats .stat {
  font-size: 2rem;
  font-weight: bold;
  color: var(--maroon);
}
.highlights-stats .stat p {
  font-size: 1rem;
  color: var(--text);
}
.highlights-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}
.highlights-cards .card {
  background: var(--card);
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 20px;
  box-shadow: var(--shadow);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.highlights-cards .card:hover {
  transform: translateY(-5px);
  box-shadow: 0 14px 28px rgba(0,0,0,.15);
}
.highlights-stats .stat {
  font-size: 2rem;
  font-weight: bold;
  color: var(--maroon);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s ease;
}
.highlights-stats .stat:hover {
  transform: scale(1.05);
}
.highlights-stats .icon {
  color: var(--maroon);
  margin-bottom: 8px;
  animation: bounce 1.5s infinite;
}
.highlights-stats .icon svg {
  width: 48px;
  height: 48px;
}

.highlights-stats .count {
  font-size: 2rem;
}
@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

/* Logo Alignment */
.site-header .container.nav-bar > a {
  display: flex;
  align-items: center;
  padding-left: 10px;
  margin: 0;
}

.site-logo {
  height: 50px; /* bigger size */
  width: auto;
  display: block;
}

@media (max-width: 500px) {
  .site-logo {
    height: 45px; /* slightly smaller on mobile */
  }
}

.why-grid .ic {
  color: var(--maroon); /* website primary color */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.why-grid .ic svg {
  width: 36px;
  height: 36px;
}

.why-grid li:hover .ic {
  color: var(--navy); /* hover color */
  transition: color 0.3s ease;
}

.site-header .container {
  padding-left: 0; /* remove left padding */
}

.site-header .container.nav-bar > a {
  margin-left: 0; /* remove margin from logo link */
}

.quick-contact {
  position: fixed;
  bottom: 24px;
  right: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  z-index: 9999;
  animation: slideIn 0.8s ease-out;
}

.quick-contact .btn {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: 0 4px 10px rgba(0,0,0,0.25);
  transition: transform 0.3s ease, background-color 0.3s ease;
  background-color: #666;
  overflow: hidden;
}

.quick-contact .btn .icon {
  width: 28px;
  height: 28px;
}

.quick-contact .btn:hover {
  transform: scale(1.15) rotate(8deg);
}

.quick-contact .btn .tooltip {
  position: absolute;
  right: 70px;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0,0,0,0.85);
  color: white;
  padding: 6px 10px;
  font-size: 13px;
  border-radius: 6px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.quick-contact .btn:hover .tooltip {
  opacity: 1;
}

/* Colors for specific buttons */
.quick-contact .whatsapp {
  background-color: #25D366;
}
.quick-contact .whatsapp:hover {
  background-color: #1EBE5B;
}

.quick-contact .call {
  background-color: #E63946;
}
.quick-contact .call:hover {
  background-color: #CC2E3C;
}

/* Slide-in animation */
@keyframes slideIn {
  from {
    transform: translateX(100px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


.banner-container {
  position: relative;
  max-width: 100%;
  height: 400px;
  overflow: hidden;
}

.banner-slide {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
}

.banner-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner-text {
  position: absolute;
  bottom: 20px;
  left: 30px;
  background: rgba(0,0,0,0.5);
  color: white;
  padding: 12px 20px;
  border-radius: 6px;
  font-size: 18px;
}

.fade {
  animation: fadeEffect 1s ease-in-out;
}

@keyframes fadeEffect {
  from { opacity: 0.4; }
  to { opacity: 1; }
}


.banner {
    background: linear-gradient(135deg, #6a11cb 0%, #2575fc 100%);
    text-align: center;
    padding: 30px;
}

.main-logo {
    width: 150px;
    margin-bottom: 20px;
}

.icon-row {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.banner-icon {
    width: 60px;
    height: 60px;
    transition: transform 0.3s ease, filter 0.3s ease;
}

.banner-icon:hover {
    transform: scale(1.2) rotate(10deg);
    filter: drop-shadow(0 4px 6px rgba(0,0,0,0.3));
}
